const actions = {
  getPeriods: (page, search, filter) => ({
    type: 'GET_PERIODS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  storeAutoGeneratePeriod: data => ({
    type: 'STORE_AUTO_GENERATE_PERIOD_REQUEST',
    payload: { body: data }
  }),
  storeAdjustingJournalEntry: data => ({
    type: 'STORE_ADJUSTING_JOURNAL_ENTRY_REQUEST',
    payload: { body: data }
  }),
  closeAllPeriod: data => ({
    type: 'CLOSE_ALL_PERIOD_REQUEST',
    payload: { body: data }
  }),
  editPeriod: id_period => ({
    type: 'EDIT_PERIOD_REQUEST',
    payload: { id_period: id_period }
  }),
  updatePeriod: (id_period, data) => ({
    type: 'UPDATE_PERIOD_REQUEST',
    payload: {
      id_period: id_period,
      body: data
    }
  }),
  showPeriod: id_period => ({
    type: 'SHOW_PERIOD_REQUEST',
    payload: { id_period: id_period }
  }),
  closePeriod: (id_period,body) => ({
    type: 'CLOSE_PERIOD_REQUEST',
    payload: { id_period, body }
  }),
  reopenPeriod: (id_period,body) => ({
    type: 'REOPEN_PERIOD_REQUEST',
    payload: { id_period, body }
  }),
  closeFAPeriod: (id_period,body) => ({
    type: 'CLOSE_FA_PERIOD_REQUEST',
    payload: { id_period, body }
  }),
  reopenFAPeriod: (id_period,body) => ({
    type: 'REOPEN_FA_PERIOD_REQUEST',
    payload: { id_period, body }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
