import React, { Component } from "react";

import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import { Form, InputNumber, Modal, Col, Input, Select } from "antd";

const InputGroup = Input.Group;
const { Option } = Select;
// const { TextArea } = Input;
class ModalClosePeriod extends Component {
  
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        const { currencies } = this.props;
        const { getFieldValue } = this.props.form;
        let curr_post = [];
        let new_value = {currencies:[]}
        
        currencies.map((data, index) =>{
          curr_post.push({
            id_currency : getFieldValue(`id_currency[${index}]`),
            closing_rate : getFieldValue(`closing_rate[${index}]`),
          })
        })

        new_value.currencies = curr_post;
        this.props.submit(new_value);
      }
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const { currencies, loading } = this.props;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const formItems = currencies.map((currency, index) => (
      <InputGroup
        key={index}
      >
        <Col span={12}>  
          <Form.Item
            {...formItemLayout}
            label={<IntlMessages id="currencies.pageHeader" />}
            //required={false}
            key={index}
          >
            {getFieldDecorator(`id_currency[${index}]`, {
              initialValue: currency.id_currency,
              rules: [{
                required: true,
                //whitespace: true,
                message: <IntlMessages id="required" />,
              }],
            })(
              <Select
                  //loading={loading}
                  showSearch
                  style={{ width:"100%" }}
                  //onChange={this.handleChangeBaseUnit}
                  optionFilterProp="children"
                  disabled={true}
                >
                  {currencies.map((data, key) => (
                    <Option key={key} value={data.id_currency}>
                      { data.currency_code + " (" + data.currency_name + ")"}
                    </Option>
                    ))
                  }
                </Select>
            )}
          
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item
            {...formItemLayout}
            label={<IntlMessages id="closingRate" />}
            //required={false}
            key={index}
          >
            {getFieldDecorator(`closing_rate[${index}]`, {
              //validateTrigger: ['onChange', 'onBlur'],
              //initialValue:,
              rules: [{
                required: true,
                message: <IntlMessages id="required" />,
              }],
            })(
              <InputNumber
                formatter={value =>
                  `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                }
                parser={value => value.replace(/\$\s?|(,*)/g, "")}
                min={0.01}
                precision={2}
                style={{ width: "100%" }}
              />
            )}  
          </Form.Item>
        </Col>
      </InputGroup>
    ));

    return (
      <Modal
        title={<IntlMessages id="closePeriod" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="40%"
        okText={<IntlMessages id="button.submit"></IntlMessages>}
        cancelText={<IntlMessages id="button.cancel"></IntlMessages>}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          {formItems}
        </Form>
      </Modal>
    );
  }
}

const WrappedModalClosePeriod = Form.create({ name: "modal_close_period" })(
  ModalClosePeriod
);

export default WrappedModalClosePeriod;
