import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Redirect } from "react-router-dom";

import PeriodUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin, Tooltip } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/generalLedger/period/actions";
import { Box } from "../../../App/app.style";
import History from "./history";

const redirectTo = "/dashboard/setup/periods";


class UpdatePeriod extends Component {
  state = {
    visible: false
  }
  // Get All data diawal
  componentDidMount() {
    const { editPeriod } = this.props;
    const { id } = this.props.match.params;
    editPeriod(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.period.error) {
      notification("error", this.props.period.error);
      this.props.clearError();
    }

    // // Display success notif
    // if (this.props.period.success) {
    //   this.props.history.replace(redirectTo)
    // }
    // Display success notif
    if (this.props.period.success) {
      notification("success", this.props.period.success);
      this.props.clearSuccess();
    }
  }

  handleClosePeriod = (value) => {
    const { closePeriod } = this.props;
    const { id } = this.props.match.params;
    closePeriod(id, value);
  }

  handleReopenPeriod = () => {
    const { reopenPeriod } = this.props;
    const { id } = this.props.match.params;
    reopenPeriod(id);
  }

  handleCloseFAPeriod = () => {
    const { closeFAPeriod } = this.props;
    const { id } = this.props.match.params;
    closeFAPeriod(id);
  }

  handleReopenFAPeriod = () => {
    const { reopenFAPeriod } = this.props;
    const { id } = this.props.match.params;
    reopenFAPeriod(id);
  }

  handleModalHistory = () => {
    this.setState({ visible: !this.state.visible })
  };

  render() {
    const { loading, period } = this.props.period;

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="periods.update.title" />}
            style={{ margin: 0 }}
          >
            <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
              </Button>
            </Tooltip>

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <History
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={period ? period.description : null}
          />

          {this.props.period.loadingEdit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
              <PeriodUpdateForm
                auth={this.props.auth}
                period={this.props.period}
                closePeriod={this.handleClosePeriod.bind(this)}
                reopenPeriod={this.handleReopenPeriod.bind(this)}
                closeFAPeriod={this.handleCloseFAPeriod.bind(this)}
                reopenFAPeriod={this.handleReopenFAPeriod.bind(this)}
              />
            )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { auth: state.auth, period: state.period };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdatePeriod);
