import React, { Component } from "react";
import { Timeline, Modal, Row, Col } from "antd";
import { Link } from "react-router-dom";

import theme from "../../../../settings/themes/themedefault";
import IntlMessages from "../../../../components/utility/intlMessages";

// const redirectPR = "/dashboard/purchases/purchase-receipts";

class HistoryPurchaseOrder extends Component {
  constructor(props) {
    super(props);
    this.state = {
      history: [],
      desc: null
    };
  }

  componentDidUpdate(prevProps) {
    const { description } = this.props;
    const { desc } = this.state;
    if (prevProps.description !== desc && description) {
      const spliteds = description.split("|").reverse();

      this.setState({
        history: [...spliteds],
        desc: description
      });
    }
  }

  render() {
    const { history } = this.state;

    return (
      <Modal
        title={<IntlMessages id="periods.history" />}
        centered
        visible={this.props.visible}
        footer={false}
        width="50%"
        onCancel={this.props.handleModalHistory}
      >
        <Row>
          <Col span={5}></Col>
          <Col span={19}>
            <Timeline>
              { 
                history.map((data, index) => {
                  return <Timeline.Item key={index}>
                    {
                      (data ? data : "")
                    }
                  </Timeline.Item>
                })
              }    
            </Timeline>
          </Col>
        </Row>
      </Modal>
    );
  }
}

export default HistoryPurchaseOrder;
