import React, { Component } from "react";
import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Divider, Row, Col , Alert, DatePicker, InputNumber, Popconfirm } from "antd";
import ModalClosePeriod from "./modalClosePeriod";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
// const { TextArea } = Input;
const InputGroup = Input.Group;

class PeriodUpdateForm extends Component {

  state = {
    visibleModalClosePeriod: false
  }

  handleModalClosePeriod = () => {
    this.setState({
      visibleModalClosePeriod : !this.state.visibleModalClosePeriod
    })
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    //console.log(this.props.period.currencies);
    const { getFieldDecorator } = this.props.form;
    const { period } = this.props;
    const { loading, currencies } = period;
    const data = period.period ? period.period : [];
    const period_end_rates = data.period_end_rates ? data.period_end_rates : []  
    const used = data.is_used;
    //const error = period.errorField ? period.errorField : [];
    //const spliteds = period.period ? period.period.description.split("|").reverse() : []

    const formItems = period_end_rates.map((currency, index) => (
      <InputGroup
        key={index}
      >
        <Col span={12}>  
          <Form.Item
            {...formItemLayout}
            label={<IntlMessages id="currencies.pageHeader" />}
            key={index}
          >
            {getFieldDecorator(`id_currency[${index}]`, {
              initialValue: currency.id_currency,
            })(
              <Select
                  showSearch
                  style={{ width:"100%" }}
                  optionFilterProp="children"
                  disabled={true}
                >
                  {currencies.map((data, key) => (
                    <Option key={key} value={data.id_currency}>
                      { data.currency_code + " (" + data.currency_name + ")"}
                    </Option>
                    ))
                  }
                </Select>
            )}
          
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item
            {...formItemLayout}
            label={<IntlMessages id="closingRate" />}
            //required={false}
            key={index}
          >
            {getFieldDecorator(`closing_rate[${index}]`, {
              initialValue: parseInt(currency.closing_rate),
            })(
              <InputNumber
                formatter={value =>
                  `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                }
                parser={value => value.replace(/\$\s?|(,*)/g, "")}
                min={0.01}
                precision={2}
                style={{ width: "100%" }}
                disabled={true}
              />
            )}  
          </Form.Item>
        </Col>
      </InputGroup>
    ));

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <>
        <ModalClosePeriod
          visible={this.state.visibleModalClosePeriod}
          handleModal={this.handleModalClosePeriod}
          submit={this.props.closePeriod}
          currencies={currencies}
          loading={loading}
        />
        

        <Form layout="vertical" onSubmit={this.handleSubmit}>
          {used ? (
            <Row style={{ marginBottom: "20px" }}>
              <Col span={24}>
                <Alert
                  message={<IntlMessages id="periods.help" />}
                  type="warning"
                  showIcon
                />
              </Col>
            </Row>
          ) : (
            ""
          )}
          {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          </Divider> */}

          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="periods.name" />}
              >
                {getFieldDecorator("period_name", {
                  initialValue: data.period_name,
                })(<Input
                  disabled={true}
                />)}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="beginDate" />}
              >
                {getFieldDecorator("begin_date", {
                  initialValue: moment(data.begin_date, "YYYY-MM-DD")
                })(
                  <DatePicker
                    style={{ width: "100%" }}
                    allowClear={false}
                    disabled={true}
                    // showTime={{ format: "HH:mm:ss" }}
                    format="DD MMMM YYYY"
                    disabledDate={e => e.valueOf() > moment()}
                  />
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="endDate" />}
              >
                {getFieldDecorator("end_date", {
                  initialValue: moment(data.end_date, "YYYY-MM-DD")
                })(
                  <DatePicker
                    style={{ width: "100%" }}
                    allowClear={false}
                    disabled={true}
                    // showTime={{ format: "HH:mm:ss" }}
                    format="DD MMMM YYYY"
                    disabledDate={e => e.valueOf() > moment()}
                  />
                )}
              </Form.Item>

            </div>

            <Divider type="vertical" style={{ height: "40vh" }} />

            <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>
              {data.is_closed === 1
                ? formItems
                : null
              }
            </div>
          </div>

          {
            data.is_closed 
            ?
              ( 
                <Popconfirm
                  title={<IntlMessages id="confirmReopenPeriod" />}
                  onConfirm={() =>
                    this.props.reopenPeriod()
                  }
                  okText={getLanguage({ id: "yes" })}
                  cancelText={getLanguage({ id: "no" })}
                >
                  <Button
                    type="default"
                    style={{ marginTop: "20px", marginLeft: "50px", backgroundColor:"#00B16A", color:"#FFFFFF" }}
                    loading={loading}
                    //onClick={this.props.reopenPeriod}
                  >
                    <IntlMessages id="reopenPeriod" />
                  </Button>
                </Popconfirm>
                
              )
            : currencies.length > 0
              ?
                (              
                  <Button
                    type="primary"
                    style={{ marginTop: "20px", marginLeft: "50px" }}
                    loading={loading}
                    onClick={this.handleModalClosePeriod}
                  >
                    <IntlMessages id="closePeriod" />
                  </Button>
                )
              : 
                (
                  <Popconfirm
                    title={<IntlMessages id="confirmClosePeriod" />}
                    onConfirm={() =>
                      this.props.closePeriod()
                    }
                    okText={getLanguage({ id: "yes" })}
                    cancelText={getLanguage({ id: "no" })}
                  >
                    <Button
                      type="primary"
                      style={{ marginTop: "20px", marginLeft: "50px" }}
                      loading={loading}
                      // onClick={this.handleModalClosePeriod}
                    >
                      <IntlMessages id="closePeriod" />
                    </Button>
                  </Popconfirm>
                )
          }

          {
            data.is_fa_closed 
            ?
              (
                <Popconfirm
                  title={<IntlMessages id="confirmReopenFAPeriod" />}
                  onConfirm={() =>
                    this.props.reopenFAPeriod()
                  }
                  okText={getLanguage({ id: "yes" })}
                  cancelText={getLanguage({ id: "no" })}
                >
                  <Button
                    type="default"
                    style={data.is_closed === 1 
                      ? {marginTop: "20px", marginLeft: "50px"} 
                      : { marginTop: "20px", marginLeft: "50px", backgroundColor:"#00B16A", color:"#FFFFFF" }
                    }
                    loading={loading}
                    disabled={data.is_closed === 1 ? true : false}
                    //onClick={}

                  >
                    <IntlMessages id="reopenFAPeriod" />
                  </Button>
                </Popconfirm>
                
              )
            :
              (
                <Popconfirm
                  title={<IntlMessages id="confirmCloseFAPeriod" />}
                  onConfirm={() =>
                    this.props.closeFAPeriod()
                  }
                  okText={getLanguage({ id: "yes" })}
                  cancelText={getLanguage({ id: "no" })}
                >
                  <Button
                    type="primary"
                    style={{ marginTop: "20px", marginLeft: "50px" }}
                    loading={loading}
                  // onClick={this.props.closeFAPeriod}
                  >
                    <IntlMessages id="closeFAPeriod" />
                  </Button>
                </Popconfirm>
              
              )            
          }
        </Form>
      </>
    );
  }
}

const WrappedPeriodUpdateForm = Form.create({ name: "update_period" })(PeriodUpdateForm);

export default WrappedPeriodUpdateForm;
